<template>
  <div class="pos">
    <div>
      <el-row >
        <el-col :span='7' id="order">
          <el-tabs>
            <el-tab-pane label="点餐">                       
               <!-- show-summary属性可以展现合计结果 -->
              <el-table :data="tableData" border  style="width: 100%" >
                <el-table-column prop="goodsName" label="商品"  ></el-table-column>
                <el-table-column prop="count" label="量" width="50"></el-table-column>
                <el-table-column prop="price" label="金额" width="70"></el-table-column>
                <el-table-column  label="操作" width="100" fixed="right">
                  <template scope="scope">
                      <el-button type="text" size="small" @click='removeOrderList(scope.row)'>删除</el-button>
                      <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                  </template>
                </el-table-column>
              </el-table>  
              <div class="totalCountMoney">
                <sapn >数量：{{totalCount}}</sapn>&nbsp;&nbsp;&nbsp;&nbsp;
                <sapn >合计：{{totalMoney}}</sapn>
              </div>
            </el-tab-pane>
            <el-tab-pane label="挂单">
            挂单
            </el-tab-pane>
            <el-tab-pane label="外卖">
            外卖
          </el-tab-pane>
          <div class="butt">
            <el-button type="warning" @click="shortStay">挂单</el-button>
            <el-button type="danger" @click="removeAllOrders(tableData)">删除</el-button>
            <el-button type="success" @click="checkSum">结账</el-button>
          </div>
          
          </el-tabs>
        </el-col>
        <el-col :span='17' id="proList">
          <div>
            <div class="title">常用商品</div>
            <div class="often-goods-list">
              <ul>
                <li v-for="item in oftenGoods" @click="addOrderList(item)">
                  <span>{{item.goodsName}}</span>
                  <span class="price">¥{{item.price}}元</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="goods-type">
            <el-tabs>
                <el-tab-pane label="汉堡">
                  <div>
                    <ul class='cookList'>
                      <li v-for="type in type0Goods" @click="addOrderList(type)">
                          <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
                          <span class="foodName">{{type.goodsName}}</span>
                          <span class="foodPrice">￥{{type.price}}元</span>
                      </li>
                    </ul>
                  </div>
                </el-tab-pane>
                    <el-tab-pane label="小食">
                      <ul class='cookList'>
                        <li v-for="type in type1Goods" @click="addOrderList(type)">
                            <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
                            <span class="foodName">{{type.goodsName}}</span>
                            <span class="foodPrice">￥{{type.price}}元</span>
                        </li>
                      </ul>
                </el-tab-pane>
                <el-tab-pane label="饮料">
                  <ul class='cookList'>
                    <li v-for="type in type2Goods" @click="addOrderList(type)">
                        <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
                        <span class="foodName">{{type.goodsName}}</span>
                        <span class="foodPrice">￥{{type.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
                <el-tab-pane label="套餐">
                  <ul class='cookList'>
                    <li v-for="type in type3Goods" @click="addOrderList(type)">
                        <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
                        <span class="foodName">{{type.goodsName}}</span>
                        <span class="foodPrice">￥{{type.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
            </el-tabs>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  names:'pos',
  data(){
    return{
      tableData: [],
      oftenGoods:[],
      type0Goods:[],
      type1Goods:[],
      type2Goods:[],
      type3Goods:[],
      totalMoney:0,
      totalCount:0,
    }
  },
  created:function(){
    axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
    .then(response=>{
       console.log(response);
      this.oftenGoods=response.data;
    })
    .catch(error=>{
      alert('网络错误')
    })
    axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods')
    .then(response=>{
       console.log(response);
      this.type0Goods=response.data[0];
      this.type1Goods=response.data[1];
      this.type2Goods=response.data[2];
      this.type3Goods=response.data[3];
    })
    .catch(error=>{
      alert('网络错误')
    })
  },
  mounted:function(){
    var orderheight = document.body.clientHeight;
    console.log(orderheight);
    document.getElementById('order').style.height=orderheight+'px';
    //截取字符串
    

  },
  methods:{
    addOrderList:function(goods){
      this.totalMoney=0;
      this.totalCount=0;
      //判断商品是否在列表中
      let isHave = false;
      for (let i=0;i<this.tableData.length;i++){
        if(this.tableData[i].goodsId == goods.goodsId){
          isHave = true;
          //var thisPrice = this.goods.price;
        }
      };
      //根据判断的值写业务逻辑
      if(isHave){
        //找到符合goodsid相等的对象
        let arr = this.tableData.filter(a=>a.goodsId==goods.goodsId);
        //列表中商品存在时增加数量
        arr[0].count++;
        //arr[0].price=arr[0].count*thisPrice;
      }else{
        let newgood = {goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
        this.tableData.push(newgood);
      }
      this.getAllMoneyCount();
    },
    //删掉单行数据
    removeOrderList:function(index){
      console.log(index)
      this.tableData.splice(index,1);
      this.getAllMoneyCount();
    },
    //删掉所有数据
    removeAllOrders(tableData){
      if(this.tableData.length == 0){
        this.$message('当前没有可删除的商品');
      }else{
        this.tableData=[];
        this.totalMoney=0;
        this.totalCount=0;
      }
      
    },
     //计算总数量和金额
    getAllMoneyCount(){
      this.totalMoney=0;
      this.totalCount=0;
      if(this.tableData){      
        this.tableData.forEach((element) => {
        this.totalCount+=element.count;
        this.totalMoney=this.totalMoney+(element.price*element.count);
        });
      }
    },
    //结账
    checkSum(){
      if(this.tableData.length == 0){
        this.$message('当前没有可结账的商品');
      }else{
        this.tableData=[];
        this.totalMoney=0;
        this.totalCount=0;
        this.$message({
          showClose: true,
          message: '结账成功',
          type: 'success'
        });
      }     
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#order{
  background-color: aliceblue;
  border:1px solid #bbb;
  float: left;
  padding: 5px;
}
#proList{
  float: left;
  height:100%;
}
.butt{
  margin-top:20px;
}
.title{
  height: 20px;
  border-bottom:1px solid #D3DCE6;
  background-color: #F9FAFC;
  padding:10px;
   }
.often-goods-list ul li{
  list-style: none;
  float:left;
  border:1px solid #E5E9F2;
  padding:10px;
  margin:5px;
  background-color:#fff;
}
.o-price{
  color:#58B7FF; 
}
.price{
  color:#409EFF;
}
.goods-type{
  clear: both;
}
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
   }
   .cookList li span{
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 14px;
       padding-left: 10px;
       color:brown;
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .totalCountMoney{
     background-color: #fff;
     padding:10px;
   }
</style>



